.post {
    max-width: 600px;
    background-color: white;
    border: 1px solid lightgray;
    margin-bottom: 45px;
    justify-content: center;
    
}

.post_image {
    width: 100%;
    object-fit: contain;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    
}

.post_text {
    font-weight: normal;
    padding: 10px;
}

.post_header {
    display: flex;
    padding: 10px;
}